<template>
    <v-form ref="form" v-model="valid" lazy-validation>
      <v-text-field
        v-model="phone"
        :counter="11"
        :rules="phoneRules"
        placeholder="89991234567"
        label=" "
        required
      ></v-text-field>
  
      <v-switch
        v-model="switch1"
        inset
        label="  "
      ></v-switch>
  
      <v-text-field
        :disabled="!switch1"
        v-model="text"
        :rules="textRules"
        label=" "
        required
      ></v-text-field>
  
      <v-checkbox
        v-model="checkbox"
        :rules="[(v) => !!v || ' !']"
        label=" ?"
        required
      ></v-checkbox>
  
      <v-btn
        :disabled="!valid"
        color="success"
        class="mr-4 mt-2"
        @click="validate"
      >
        
      </v-btn>
  
      <v-btn color="error" class="mr-4 mt-2" @click="reset">
         
      </v-btn>
  
      <v-btn color="warning" class="mt-2" @click="resetValidation">
         
      </v-btn>
    </v-form>
  </template>
  <script>
  export default {
    name: "TestCase2",
    data: () => ({
      valid: true,
      phone: "",
      phoneRules: [
        (v) => !!v || "    ",
        (v) => (v && v.length > 5 && v.length < 10) || "       11 ",
        (v) => /8[0-9]*/.test(v) || "    "
      ],
      textRules: [
        (v) => !!v || "   ",
      ],
      switch1: false,
      select: null,
      items: ["", "", "", " "],
      checkbox: false,
    }),
  
    methods: {
      validate() {
        this.$refs.form.validate();
        this.getResult()
      },
      reset() {
        this.$refs.form.reset();
      },
      resetValidation() {
        this.$refs.form.resetValidation();
      },
      getResult(){
        this.$emit("result",{
          :this.phone,
          "?":this.switch1? "":" ",
          :this.text,
          ' ?':this.checkbox? '':''
        })
      },
    },
  };
  </script>