<template>
  <v-form ref="form" v-model="valid" lazy-validation>
    <v-text-field
      v-model="name"
      :counter="10"
      :rules="nameRules"
      label=""
      required
    ></v-text-field>

    <v-text-field
      v-model="email"
      :rules="emailRules"
      label="E-mail"
      required
    ></v-text-field>

    <v-select
      v-model="select"
      :items="items"
      :rules="[(v) => !!v || '  ']"
      label=""
      required
    ></v-select>

    <v-checkbox
      v-model="checkbox"
      :rules="[(v) => !!v || ' !']"
      label=" ?"
      required
    ></v-checkbox>

    <v-btn
      :disabled="!valid"
      color="success"
      class="mr-4 mt-2"
      @click="validate"
    >
      
    </v-btn>

    <v-btn color="error" class="mr-4 mt-2" @click="reset">
       
    </v-btn>

    <v-btn color="warning" class="mt-2" @click="resetValidation">
       
    </v-btn>
  </v-form>
</template>
<script>
export default {
  name: "TestCase",
  data: () => ({
    valid: true,
    name: "",
    nameRules: [
      (v) => !!v || "   ",
      (v) => (v && v.length <= 10) || "    10 ",
    ],
    email: "",
    emailRules: [
      (v) => !!v || "E-mail ",
      (v) => /.+@.+\..+/.test(v) || "E-mail   ",
    ],
    select: null,
    items: ["", "", "", " "],
    checkbox: false,
  }),

  methods: {
    validate() {
      this.$refs.form.validate();
      this.$emit("result",{
        :this.name,
        Email:this.email,
        :this.select,
        ' ?':this.checkbox? '':''
        })
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    },
  },
};
</script>